import React, { useState } from 'react'

export default function DashBoardList() {

    let [tab,setTab] = useState('')

    return (
        <div className='max-w-[1320px] mx-auto py-5'>
            <div className="md:flex">
                <ul className="flex-column space-y space-y-4 text-sm font-medium text-gray-500 dark:text-gray-400 md:me-4 mb-4 md:mb-0">
                    <li>
                        <button onClick={()=>setTab('dashboard')} className="w-[200px] inline-flex items-center px-4 py-3 text-white bg-amber-400 rounded-lg active dark:bg-blue-600" aria-current="page">
                            My Dashboard
                        </button>
                    </li>
                    <li>
                        <button onClick={()=>setTab('order')} className="inline-flex items-center px-4 py-3 rounded-lg hover:text-gray-900 bg-black hover:bg-gray-100 w-full text-white dark:bg-gray-800 dark:hover:bg-gray-700 dark:hover:text-white">
                            Orders
                        </button>
                    </li>
                </ul>
                <div className="p-6 bg-gray-50 text-medium text-gray-500 dark:text-gray-400 dark:bg-gray-800 rounded-lg w-full">
                    <div>
                        <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-2">My Dashboard</h3>
                        <p className="mb-2">This is some placeholder content the Profile tab's associated content, clicking another tab will toggle the visibility of this one for the next.</p>
                        <p>The tab JavaScript swaps classes to control the content visibility and styling.</p>
                    </div>
                    <div className='hidden'>
                        <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-2">Order</h3>
                        <p className="mb-2">This is some placeholder content the Profile tab's associated content, clicking another tab will toggle the visibility of this one for the next.</p>
                        <p>The tab JavaScript swaps classes to control the content visibility and styling.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
